import React, {useEffect, useState} from 'react';
import {Header} from './Header';
import {TaskBlock} from './TaskBlock';
import {AddNewItem} from "./AddNew";
import {AuthContext} from "../App";

export const Tasks = () => {
    const names = ['Les', 'Mike']
    const [error, setError] = useState(null);
    const [loading, setLoading] = useState(true)
    const [tasks, setTasks] = useState([])
    const { state: authState } = React.useContext(AuthContext);

    async function GetTasks() {
        setLoading(true);
        return await fetch(`https://server.smarthome.mjfullstack.com/tasks`, {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${authState.token}`
            },
            method: 'GET'
        }).then(response =>
            response.json().then((data) => {
                setTasks(data)
            })).catch((e) => {
            setError(e);
        }).then(() => {
            setTimeout(() => {
                setLoading(false)
            }, 400);
        })
    }

    async function addTask(name, task) {
        await fetch(`https://server.smarthome.mjfullstack.com/tasks`, {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${authState.token}`
            },
            method: 'POST',
            body: JSON.stringify({name, task})
        })
            .then(response =>
                response.json().then((data) => {
                    setTasks(data)
                })).catch((e) => {
                setError(e);
            })
    }

    async function deleteTask(id) {
        await fetch(`https://server.smarthome.mjfullstack.com/tasks/${id}`, {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${authState.token}`
            },
            method: 'DELETE',
            body: JSON.stringify({id})
        })
            .then(response =>
                response.json().then((data) => {
                    setTasks(data)
                })).catch((e) => {
                setError(e);
            })
    }

    useEffect(() => {
        GetTasks();
        return () => {
            setTasks([]);
        }
    }, []);

    return <div className="max-h-screen w-screen overflow-hidden">
        <Header text={'Tasks'} back={true}/>
        <hr className="mt-5"/>
        <div className="flex flex-row h-full justify-center">
            {error ? "failed to load tasks, are you authorised to view them?" : names.map((name) => {
                return (
                    <div className="flex flex-col justify-start items-center w-full mt-5"  key={name + 'mb'}>

                        <div className="font-bold text-white text-4xl"  key={name + 'ib'}>{name}</div>
                        {loading ? <div className="centerCol w-full" key={name + 'loading'}>
                                <div className="lds-ellipsis">
                                    <div/>
                                    <div/>
                                    <div/>
                                    <div/>
                                </div>
                                <AddNewItem newItem={addTask}  key={name + 'add'}/></div> :
                            <TaskBlock name={name} tasks={tasks[name]} key={name + 'tb'} DeleteFunc={deleteTask} AddFunc={addTask}/>}
                    </div>
                )
            })}
        </div>
    </div>
}