import {BrowserRouter as Router, Route} from "react-router-dom";
import {MenuScreen} from './components/Menu';
import {Heating} from './components/Heating';
import {Tasks} from './components/Tasks';
import {Shopping} from './components/Shopping';
import Cams from "./components/Cams";
import Login from "./components/Login";
import React from "react";
import {Recipes} from "./components/Recipes";

export const AuthContext = React.createContext(); // added this
const initialState = {
    isAuthenticated: false,
    // user: null,
    token: null,
};
const reducer = (state, action) => {
    switch (action.type) {
        case "LOGIN":
            // localStorage.setItem("user", JSON.stringify(action.payload.user));
            localStorage.setItem("token", JSON.stringify(action.payload.token));
            return {
                ...state,
                isAuthenticated: true,
                // user: action.payload.user,
                token: action.payload.token
            };
        case "LOGOUT":
            localStorage.clear();
            return {
                ...state,
                isAuthenticated: false,
                // user: null
            };
        default:
            return state;
    }
};

function App() {
    const [state, dispatch] = React.useReducer(reducer, initialState);
    return (
        <div className="app_body">

            <AuthContext.Provider
                value={{
                    state,
                    dispatch
                }}>
                {!state.isAuthenticated ? <Login/> :
                    <Router>
                        <div className={'container'}>
                            <Route exact path="/" component={MenuScreen}/>
                            <Route path="/heating" component={Heating}/>
                            <Route path="/tasks" component={Tasks}/>
                            <Route path="/shopping" component={Shopping}/>
                            <Route path="/cam" component={Cams}/>
                            <Route path="/recipes" component={Recipes}/>
                            <Route path="/map" component={() => {
                                window.location.href = 'https://goo.gl/maps/SZnfFiGk2VhpDoWV9';
                                return null;
                            }}/>
                        </div>
                    </Router>}
            </AuthContext.Provider>
        </div>
    );

}

export default App;
