import React, {useEffect, useState} from 'react';
import {Header} from './Header';
import {ShoppingListBlock} from "./ShoppingListBlock";
import {AddNewItem} from "./AddNew";
import {AuthContext} from "../App";
import {RecipeBlock} from "./RecipeBlock";

export const Recipes = () => {
        const [list, setInfo] = useState([])
        const [error, setError] = useState(null);
        const [loading, setLoading] = useState(true)
        const [newItem, setNewItem] = useState('')
        const {state: authState} = React.useContext(AuthContext);

        async function GetInfo() {
            try {
                setLoading(true);
                await fetch(`https://server.smarthome.mjfullstack.com/recipes`, {
                    headers: {
                        'Content-Type': 'application/json',
                        'Authorization': `Bearer ${authState.token}`
                    },
                    method: 'GET'
                }).then(response =>
                    response.json().then((data) => {
                        setInfo(data);

                    }));
            } catch (e) {
                setError(e);
            } finally {
                setTimeout(() => {
                    setLoading(false);
                }, 500)
            }
        }

        async function addItem(item) {
            await fetch(`https://server.smarthome.mjfullstack.com/recipes`, {
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': `Bearer ${authState.token}`
                },
                method: 'POST',
                body: JSON.stringify(item)
            })
                .then(response =>
                    response.json().then((data) => {
                        setInfo(data);
                    }));
        }

        const handleSubmit = (e) => {
            e.preventDefault();
            if (newItem) {
                addItem(newItem);
                setNewItem('');

            }
        }

        async function deleteItem(id, name) {
            await fetch(`https://server.smarthome.mjfullstack.com/recipes/${id}`, {
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': `Bearer ${authState.token}`
                },
                method: 'DELETE',
                body: JSON.stringify({id})
            })
                .then(response =>
                    response.json().then((data) => {
                        setInfo(data)
                    }));
        }

        useEffect(() => {
            GetInfo();

        }, []);

        if (error) return "Failed to load shopping list";
        return (
            <div className="max-h-screen max-w-screen">
                <Header text={'Recipes'} back={true}/>
                <hr className="mt-5"/>
                {loading ? <div className="centerCol w-full">
                        <div className="lds-ellipsis">
                            <div/>
                            <div/>
                            <div/>
                            <div/>
                        </div>
                    </div> :
                    <RecipeBlock list={list} deleteItem={deleteItem}/>}
                <AddNewItem handleSubmit={handleSubmit} newItem={newItem} setNewItem={setNewItem}/>
            </div>
        );
    }
;