import React, {useRef, useContext, useState} from 'react';
import {useHistory} from "react-router-dom";
import {AuthContext} from "../App";

const Login = () => {
    const {dispatch} = useContext(AuthContext);
    const initialState = {
        username: "",
        password: "",
        isSubmitting: false,
        errorMessage: null
    };

    const [data, setData] = useState(initialState);
    const handleInputChange = event => {
        setData({
            ...data,
            [event.target.name]: event.target.value
        });
    };
    let history = useHistory();
    const form = useRef(null)
    const handleSubmit = (evt) => {
        evt.preventDefault();
        setData({
            ...data,
            isSubmitting: true,
            errorMessage: null
        });
        let formData = new FormData(form.current)
        fetch('https://server.smarthome.mjfullstack.com/token/',
            {
                method: 'POST',
                body: formData
            }).then(res => {
            if (res.status === 200) {
                return res.json();
            }
            throw res;
        })
            .then(resJson => {
                dispatch({
                    type: "LOGIN",
                    payload: resJson
                });
            })
            .catch(error => {
                setData({
                    ...data,
                    isSubmitting: false,
                    errorMessage: error.message || error.statusText
                });
            });
    };
    //         .then((response) => response.json().then(() => {
    //             if (response.status === 200) {
    //                 history.push('/')
    //             } else {
    //                 history.push('/login')
    //             }
    //         }).catch((error) => {
    //             history.push('/login')
    //         }))
    // }
    return (
                    <div className={"flex flex-col justify-center items-center h-full"}>
        <form ref={form} onSubmit={handleSubmit}>
                <div className={"flex flex-col justify-center items-center"}><input name="username" type="text"
                                                                                    placeholder="Username"
                                                                                    value={data.username}
                                                                                    onChange={handleInputChange}
                                                                                    className={"timeInput text-center"}
                />
                    <input name="password" type="password" placeholder="Password"
                           value={data.password}
                           onChange={handleInputChange}
                           className={"timeInput text-center"}/></div>
                <input type="submit" value="Submit"
                       className={"submitBtn"}/>
        </form></div>
    )
};

export default Login;